import uniStatusBar from "../uni-status-bar/uni-status-bar.vue";
import uniIcons from "../uni-icons/uni-icons.vue";

export default {
	name: "UniNavBar",
	components: {
		uniStatusBar,
		uniIcons
	},
	data() {
		return {
			customBarH: this.customBar,
			statusBarH:0
		}
	},
	props: {
		title: {
			type: String,
			default: ""
		},
		leftText: {
			type: String,
			default: ""
		},
		rightText: {
			type: String,
			default: ""
		},
		leftIcon: {
			type: String,
			default: ""
		},
		rightIcon: {
			type: String,
			default: ""
		},
		fixed: {
			type: [Boolean, String],
			default: false
		},
		color: {
			type: String,
			default: "#000000"
		},
		backgroundColor: {
			type: String,
			default: "#FFFFFF"
		},
		statusBar: {
			type: [Boolean, String],
			default: false
		},
		shadow: {
			type: [String, Boolean],
			default: false
		},
		border: {
			type: [String, Boolean],
			default: true
		}
	},
	mounted() {
		if (uni.report && this.title !== '') {
			uni.report('title', this.title)
		}
		uni.getSystemInfo({
			success:(res)=>{
				this.statusBarH = res.statusBarHeight
				console.log('adasda' + this.statusBarH)
			}
		})
	},
	methods: {
		onClickLeft() {
			this.$emit("clickLeft");
		},
		onClickRight() {
			this.$emit("clickRight");
		}
	},
	
};
